// Normal type
@font-face {
	font-family: 'helvetica-neue-normal';
	src: font('normal/helvetica-neue.eot');
	src: local(''),
	font('normal/helvetica-neue.woff') format('woff'),
	font('normal/helvetica-neue.ttf') format('truetype'),
	font('normal/helvetica-neue.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

